<template>
    <div class="serviceInfo" v-loading="loading" v-if="tableData">
        <div class="serviceInfo-content">
            <div class="box product-info">
                <div class="info" v-for="(item,key) in tableData.getProductInfo" :key="key">
                    <div class="list-main-left">
                        <img :src="item.smallImg" alt="">
                    </div>
                    <div class="list-main-right">
                        <p class="orderInfo">{{item.chrName}}</p>
                        <p class="gray">{{item.goodsSpec}}</p>
                        <p class="gray">×{{item.goodsNumber}}</p>
                    </div>
                </div>
                <div class="detail">
                    <p><span>{{$L('退款金额')}}：</span><b class="red">{{ tableData.refundAmount }}</b></p>
                    <p><span>{{$L('退款原因')}}：</span><span class="text">{{ tableData.busMess }}</span></p>
                    <p><span>{{$L('退款附图')}}：</span><img class="img" :src="i" alt="" v-for="i in tableData.pic"></p>
                    <p><span>{{$L('商家回复')}}：</span><span class="text">{{ tableData.refundResult }}</span></p>
                </div>
            </div>
            <div class="service-order">
                <div class="title">{{$L('服务单详情')}}</div>
                <el-timeline>
                    <el-timeline-item
                            v-for="(activity, index) in activities"
                            :key="index"
                            :timestamp="activity.date"
                            :type="activity.type"
                            :color="activity.color"
                            :size="activity.size">
                        {{activity.logInfo}}
                    </el-timeline-item>
                </el-timeline>
            </div>
            <el-form ref="form" :model="form" :rules="applyRules" class="box logistics-info" v-if="tableData.refundStatus === 2">
                <div class="title">{{$L('填写物流信息')}}</div>
                <el-form-item  prop="company">
                    <el-input size="small" :placeholder="$L('请填写物流公司名称')" v-model="form.company"></el-input>
                </el-form-item>
                <el-form-item  prop="number">
                    <el-input size="small" :placeholder="$L('请填写物流单号')" v-model="form.number"></el-input>
                </el-form-item>
            </el-form>
            <el-button type="primary" @click="submitFunc" v-if="tableData.refundStatus === 2">{{ $L('确认') }}</el-button>
            <el-button type="primary" @click="submitTwoFunc" v-if="tableData.refundStatus === 5">{{ $L('再次申请') }}</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "orderServiceInfo",
        data(){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                form:{
                    company:'',
                    number:''
                },
                applyRules: {
                    number: [
                        {
                            validator: (rule, value, callback) => {
                                value = parseInt(value);
                                if (isNaN(value)) {
                                    callback(new Error('请选输入正确的物流单号'));
                                } else {
                                    let patter = /^[0-9a-zA-Z]{15,}$/;
                                    if (!patter.test(value)) {
                                        return callback(new Error('物流单号有误'));
                                    } else {
                                        callback();
                                    }

                                }
                            }
                        },
                    ],
                },
                activities: [],
                tableData: {}
            }
        },
        created (){
          this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc() {
                this.$router.back(-1);
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let id = _self.$route.params.id;
                _self.$api('afterSaleDetail', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data.refundOrderInfo;
                        _self.activities =  reData.data.refundLog;
                        for (let i=0;i<_self.activities.length;i++){
                            _self.activities[i].type = 'primary';
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'orderList',params:{type:'all'}});
                    });
                });
            },
            submitFunc(){
                const _self = this;
                _self.loading = true;
                if(_self.form.company !== '' && _self.form.number !== ''){
                    let id = _self.tableData.refundId;
                    let arr = {
                        refundId:id,
                        deliveryName: _self.form.company,
                        deliveryNum: _self.form.number
                    };
                    _self.$api('addRefundDelivery', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.getData();
                            _self.$message({
                                type: 'success',
                                message: _self.$L('提交成功!')
                            });
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name:'orderList',params:{type:'all'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    });
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('物流公司或物流单号不存在!')
                    });
                    _self.loading = false;
                }
            },
            submitTwoFunc(){
                this.$router.push({name: "orderService", params: {id: this.$route.params.id}});
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .serviceInfo{
        .serviceInfo-content{
            background-color: #fff;
            .product-info{
                padding: 20px 10px 10px;
                font-size: 14px;
                border-bottom: 1px solid #E1E1E1;
                .info{
                    position: relative;
                    width: 100%;
                    overflow: hidden;
                    padding-bottom: 20px;
                    &:last-child{
                        padding-bottom: 0;
                    }
                    .list-main-left{
                        width: 80px;
                        height: 80px;
                        float: left;
                        img{
                            width: 80px;
                            height: 80px;
                        }
                    }
                    .list-main-right{
                        padding-left: 18px;
                        float: left;
                        width: calc(100% - 120px);
                        p{
                            line-height: 26px;
                        }
                        .orderInfo{
                            cursor: pointer;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            word-break: break-all;
                            font-size: 14px;
                            @extend .text-one-line;
                        }
                    }
                }
                .detail{
                    line-height: 30px;
                    .text{
                        display: inline-block;
                        width: 75%;
                        vertical-align: top;
                        word-wrap: break-word;
                    }
                    .img{
                        width: 65px;
                        height: 65px;
                        vertical-align: text-top;
                        margin-right: calc(100% - 90px - 65px *4);
                        &:last-child{
                            margin-right: 0;
                        }
                    }
                }
            }
            .service-order{
                padding: 20px 10px 10px;
            }
            .logistics-info{
                padding: 0 10px;
                .el-form-item{
                    .el-input{
                        width: 300px;
                    }
                }
            }
            .el-button{
                display: inline-block;
                width: 94%;
                height: 50px;
                font-size: 14px;
                margin: 20px 3% 0;
            }
            .title{
                font-size: 14px;
                margin-bottom: 10px;
            }
            .red{
                color: #ff0000;
            }
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>